import React from 'react';
import ReactDOM from 'react-dom';
import initI18n from 'src/i18n/InitI18n';

import App from './App';
import reportWebVitals from './reportWebVitals';

import './index.css';

initI18n().then(() => {
  ReactDOM.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>,
    document.getElementById('root')
  );
});

